// La Crox can styling

.la-croix-can {
    width: 39px;
    height: 75px;
    background-image: url(data:image/png;base64,);    
    display: inline-block;
    background-size: 40px;
    margin: 20px;

    &:nth-of-type(2n) {
        transform: rotate(20deg);
    }

    &:nth-of-type(3n) {
        transform: rotate(-20deg);
    }

    &:nth-of-type(5n) {
        transform: rotate(160deg);
    }

    &:hover {
        transform: none;
    }
}